// 
// i<h1>A</h1>mport { BrowserRouter as Router, Route, Link, Switch } from 'react-router-dom';
import { Route, Link, Switch } from 'react-router-dom';

// 
import './styles/global.scss';
import '../node_modules/bootstrap/dist/js/bootstrap';
// import '../node_modules/vanilla-tilt/dist/vanilla-tilt';

// 
import Navbar from './utils/Navbar';
import Breadcrumb from './utils/Breadcrumb';
import Footer from './utils/Footer';
import ScrollToTop from './utils/ScrollToTop';
import { routes } from './utils/routes';
import { API_URL, IMG_URL } from './utils/config';

// 
// https://v5.reactrouter.com/web/guides/quick-start
// - routers, like <BrowserRouter> and <HashRouter>
// - route matchers, like <Route> and <Switch>
// - and navigation, like <Link>, <NavLink> , and <Redirect>

// <Switch> :  exact
// <Route> : 
// <Link to=""> : 
// <NavLink to=""> :  (style itself active ?)
// <Redirect path=""> : 

import Auth from './pages/Auth';
import Product from './pages/Product';
import Member from './pages/Member';
import Team from './pages/Team';
import Teach from './pages/Teach';
import Game from './pages/Game';
import Cart from './pages/Cart';
import Home from './pages/Home';
import Error from './pages/Error';
import Landing from './pages/Landing';
// import Email from './pages/Email';
import Email from './components/Email/email';

// Context
import { ProductsProvider } from './utils/context/ProductsContext';
import { RWDProvider } from './utils/context/RWDContext';
import { CartProvider } from './utils/context/CartContext';
import { AuthProvider } from './utils/context/AuthContext';
import { LoadingProvider } from './utils/context/LoadingContext';
// import { OrderProvider } from './utils/context/OrderContext';

import { Modal } from '../node_modules/bootstrap/dist/js/bootstrap';

function App() {
    return (
        <>
            <RWDProvider>
                <AuthProvider>
                    <LoadingProvider>
                        <ProductsProvider>
                            {/* <CartProvider> */}
                            {/* <OrderProvider> */}
                            <div className="web-content">
                                {/*  */}
                                <Navbar />
                                <ScrollToTop />
                                {/*  */}
                                {/* <Breadcrumb /> */}
                                <Switch>
                                    {/*  */}
                                    <Route path={routes.auth}>
                                        <Auth />
                                    </Route>
                                    {/*  */}
                                    <Route path={routes.product}>
                                        <Product />
                                    </Route>
                                    {/*  */}
                                    <Route path={routes.member}>
                                        <Member />
                                    </Route>
                                    {/*  */}
                                    <Route path={routes.team}>
                                        <Team />
                                    </Route>
                                    {/*  */}
                                    <Route path={routes.teach}>
                                        <Teach />
                                    </Route>
                                    {/*  */}
                                    <Route path={routes.game}>
                                        <Game />
                                    </Route>
                                    {/* email */}
                                    <Route path={routes.email}>
                                        <Email />
                                    </Route>
                                    {/*  */}
                                    <Route path={routes.cart}>
                                        <Cart />
                                    </Route>
                                    {/*  */}
                                    <Route exact path={routes.home}>
                                        <Home />
                                    </Route>
                                    {/*  */}
                                    <Route exact path={routes.landing}>
                                        <Landing />
                                    </Route>
                                    {/* 404 */}
                                    <Route path={routes.error}>
                                        <Error />
                                    </Route>
                                </Switch>
                            </div>
                            <Footer />
                            {/* </OrderProvider> */}
                            {/* </CartProvider> */}
                        </ProductsProvider>
                    </LoadingProvider>
                </AuthProvider>
            </RWDProvider>
        </>
    );
}

export default App;

